.main {
  margin: auto;
  /* margin-top: 4em; */

}
.techsection {
  width: 100%;
 align-items: center;
 justify-content: center;
  text-align: center;
  background-color: var(--clr-bg);
  height: 55rem;
  padding: 10%;
  margin: auto;
  /* border: 1px solid blue; */
  /* display: grid;
  grid-template-columns: repeat(5,1fr);
  grid-template-rows: repeat(auto); */
}

.techsection > div {
  /* width: 11%; */
  width: 15%;
  height: 16vh;
  max-height: 140px;
  box-shadow: var(--shadow);
  z-index: 1;
  border-radius: 20px;
  margin: 2.5%;
  float: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease 0s;
  border: 2px solid var(--clr-primary);
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 10px;
}
.techsection > div:hover {
  background-color: var(--clr-bg);
  overflow: hidden;
  transform: scale(1.08);
  box-shadow: rgba(0, 0, 0, 0.36) 0px 10px 36px 10px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  border: 1px solid var(--clr-primary);
}

.techsection > div > svg {
  font-size: 8vh;
  color: var(--clr-primary);
}
.techsection > div > h5 {
  position: relative;
  top: 8px;
  background-color: transparent;
}

@media (max-width: 600px) {
  .techsection {
    height: 140vh;
  }
  .techsection > div {
    width: 40%;
    margin-right: 5%;
    /* margin: auto; */
    margin-bottom: 2%;
    height: 18vh;
  }
}
@media (max-width: 800px) {
  .techsection {
    height: 140vh;
  }
  .techsection > div {
    width: 40%;
    margin-right: 5%;
    /* margin: auto; */
    margin-bottom: 2%;
    height: 18vh;
  }
}
